<style>
	@import './userinformation.css';
</style>
<template>
	<div class="usercontain" style="z-index:-1;">
		<div class="user_form">
	    <p class="user_form_header">
	     <i class="ivu-icon ivu-icon-person"></i>
	    	
	    </p>
	    <div class="user_form_contain">
	    <p>
	    <span><span class="redstar">*</span></span>
	    <Input style="width:300px;" v-model="username">
        </Input>
        </p>
        <p><span></span><span>{{company}}</span></p>
        <p><span></span><span>{{phones}}</span></p>
        <!--<p><span></span> <Button type="text" @click="modal1=true"></Button></p>-->
        <p><span></span> <Button type="text" @click="addboss"></Button></p>
        <p>
        </p>
	     </div>
		</div>
	  <!--  -->
	<Modal
        v-model="modal1"
        title=""
        @on-ok="ok"
        @on-cancel="cancel">
          <span><span class="redstar">*</span></span><Input v-model="value" placeholder="..."></Input>
          <br /><br />
          <span><span class="redstar">*</span></span><Input v-model="value" placeholder="..."></Input>
          <br /><br />
          <span><span class="redstar">*</span></span><Input v-model="value" placeholder="..."></Input>
    </Modal>
	  <!--   -->
	   <!--  -->
	<Modal
        v-model="modaladd"
        title=""
        @on-ok="bossok"
        @on-cancel="bosscancel">
        <div style="text-align: center;">
        	<img :src="bossimgurl"/>
        </div>         
    </Modal>
	  <!--   -->
	</div>
</template>
<script>
	 var cacse = document.getElementById("lwxf_preload")
  	 var pfcfg = cacse.innerText.replace(/_e@gt@e_/g, '>').replace(/_e@lt@e_/g, '<')
	 var cfg = JSON.parse(pfcfg.trim());
	 console.log(cfg.preload.user);
	 
	export default {
        data () {
            return {
                modal1: false,
                modaladd:false,
                value: '',
                bossimgurl:'',
                username:'',
                company:'',
                phones:''
            }
        },
        methods: {
        	addboss(){
        	    this.modaladd=true;
        		var that = this;
				this.axios({
					method: 'post',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url:'/api/users/0/qrcodes/bingshopkeeper'
				}).then(function(res) {
					that.bossimgurl= 'https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket='+res.data.data.ticket			
				})
        	},
            ok () {
                this.$Message.info('');
            },
            cancel () {
                this.$Message.info('');
            },
            bossok () {
                this.$Message.info('');
            },
            bosscancel () {
                this.$Message.info('');
            },
            reloads(){
            	this.username=cfg.preload.user.username;
            	this.company=cfg.preload.company.name;
            	this.phones=cfg.preload.storecfg.serviceCall
            }
        },
        mounted() {
			this.reloads();
		}
    }
</script>





